import request from '../utils/request';
import url from '../utils/url';
import userModel from '../user/model';
import planFormat from '../subscription/planFormat';
import { DEFAULT_CURRENCY, DEFAULT_CYCLE } from '../CONSTANTS';

const requestURL = url('payments');

export const subscription = async () => {
    if (!userModel.get().Subscribed) {
        return {
            Name: 'free',
            Title: 'Proton Free',
            MaxDomains: 0,
            CouponCode: null,
            MaxAddresses: 1,
            MaxSpace: 5368709120, // 500 MB
            MaxMembers: 1,
            Amount: 0,
            Cycle: 12,
            Currency: userModel.get().Currency,
            Plans: []
        };
    }

    const data = await request.get(requestURL('subscription'));
    if (data.Code === 1000) {
        const { Name, Title } = data.Subscription.Plans.find(({ Type }) => Type === 1) || {};
        data.Subscription.Name = Name;
        data.Subscription.Title = Title;
        return data.Subscription;
    }
};

/**
 * Get payment method status
 * @return {Promise}
 */
export const status = () => request.get(requestURL('status'));

/**
 * Get payment methods in priority order
 */
export const methods = () => {
    return request.get(requestURL('methods')).then(({ PaymentMethods }) => PaymentMethods);
};

/**
 * Get plans available to user
 * @param {Function} filter callback to filter plans (default will remove vpn)
 */
export const plans = ({ Currency = DEFAULT_CURRENCY, Cycle = DEFAULT_CYCLE } = {}, noFilter) => {
    const params = { Currency, Cycle };
    return request
        .get(requestURL('plans'), { params, useCache: true })
        .then(({ Plans }) => Plans)
        .then(planFormat(params, noFilter));
};

/**
 * Send payment details to subscribe during the signup process
 * @param {Object} params
 * @param {Promise}
 */
export const verify = async (params, check) => {
    const opt = {
        ...params,
        ...(check && {
            Currency: check.Currency,
            Cycle: check.Cycle,
            Amount: check.AmountDue,
            GiftCode: (check.Coupon || {}).Code // Is null if doesn't exist
        })
    };
    return request.post(requestURL('verify'), opt);
};

/**
 * Create a subscription
 */
export const subscribe = async (params) => {
    return request.post(requestURL('subscription'), params);
};

export const updateMethod = async (params) => {
    return request.post(requestURL('methods'), params);
};

/**
 * Validate a subscription
 */
export const valid = (opt) => request.post(requestURL('subscription', 'check'), opt);



