import appProvider from '../appProvider';

export const make = (key) => (...path) => {
    return [`${appProvider.getConfig('apiUrl')}/${key}`.trim()]
        .concat(path.filter((key) => typeof key !== 'undefined'))
        .join('/');
};

export function host(baseURL) {
    const link = document.createElement('a');
    link.href = baseURL;
    return link.host;
}

/**
 * Factory to build urls for a scope
 *     ex:
 *         const getUrl = url.build('contact')
 *         getUrl(1) === xxx/contact/1
 *         getUrl(1, 2) === xxx/contact/1/2
 *         getUrl() === xxx/contact
 *
 * @param  {String} key Scope
 * @return {Function}
 */
export default (key) => make(key);



