import * as authCache from './cache';
import { setHeaders } from '../utils/request';
import pmcw from '../crypto/pmcw';
import { setItem, removeItem, getItem, clear as clearStorage } from '../crypto/secureSessionStorage';

const resetHeaders = () => {
    setHeaders({
        Authorization: undefined,
        'x-pm-session': undefined,
        'x-pm-uid': undefined
    });
};

export const read = authCache.read;
export const clear = () => {
    authCache.clear();
    resetHeaders();
    clearStorage();
};

/**
 * Store auth session informations
 * @param  {Object} data
 * @return {void}
 */
export function storeData(data = {}) {
    authCache.storeData(data);
    setAuthHeaders(data);
}

/**
 * These headers are used just once for the /cookies route,
 * then we forget them and use cookies and x-pm-session header instead.
 */
export function setAuthHeaders() {
    authCache.set('headersSet', true);
    setHeaders({ 'x-pm-uid': read('UID') });
    if (authCache.read('AccessToken')) {
        return setHeaders({ Authorization: `Bearer ${read('AccessToken')}` });
    }
    return setHeaders({ Authorization: undefined });
}

export const detectAuthenticationState = () => {
    const session = getItem('SessionToken');
    const UID = getItem('UID');

    if (UID) {
        authCache.save({ UID });
        return setAuthHeaders();
    }

    if (session) {
        authCache.save({ UID: pmcw.decode_base64(session) });
        setItem('UID', read('UID'));
        removeItem('SessionToken');
        setAuthHeaders();
    }
};

export const isHeadersSet = () => authCache.get('headersSet');
export const getHeadersKey = () => ['x-pm-session', 'Authorization', 'x-pm-uid'];



