import request from '../utils/request';
import url from '../utils/url';
import srp, { getPasswordParams } from '../crypto/srp';
import settingsModel from '../settings/model';

const requestURL = url('settings');
console.debug('@todo always return UserSettings ?');
const store = (data) => (settingsModel.set('all', data.UserSettings), data);
const srpRequest = (type, url, { opt = {}, creds, response = null }) =>
    srp[type](requestURL(url), opt, {
        creds,
        response
    }).then(store);

export const password = async (newPassword) => {
    const data = await getPasswordParams(newPassword);
    return request.put(requestURL('password'), data);
};

export const passwordUpgrade = (data) => request.put(requestURL('password', 'upgrade'), data);
export const passwordReset = (opt, creds) => srpRequest('put', 'reset', { opt, creds });
export const updateEmail = (opt, creds) => srpRequest('put', 'email', { opt, creds });
export const enableTwoFactor = (opt, creds, response) => srpRequest('post', '2fa', { opt, creds, response });
export const disableTwoFactor = (opt, creds, response) => srpRequest('put', '2fa', { creds, response });
export const disableTOTP = (opt, creds, response) => srpRequest('put', '2fa/totp', { creds, response });
export const addU2FKeyRequest = (opt = {}) => request.get(requestURL('2fa', 'u2f'), opt);
export const addU2FKeyConfirm = (opt, creds, response) => srpRequest('post', '2fa/u2f', { opt, creds, response });
export const regenerateTwoFactorCodes = (creds, response) => srpRequest('post', '2fa/codes', { creds, response });

export const removeU2FKey = (keyHandle, creds, response) =>
    srpRequest('put', `2fa/u2f/${keyHandle}`, {
        creds,
        response
    });

export const fetch = () =>
    request
        .get(requestURL())
        .then(store)
        .then((data) => data.UserSettings);
export const setLogging = (params) => request.put(requestURL('logauth'), params).then(store);
export const notify = (data) => request.put(requestURL('notify'), data).then(store);
export const updateLocale = (data) => request.put(requestURL('locale'), data).then(store);
export const setNews = (data) => request.put(requestURL('news'), data).then(store);
export const invoiceText = (data) => request.put(requestURL('invoicetext'), data).then(store);



