import _ from 'lodash';
import { isMail, isVPN } from '../utils/appType';
import { BASE_SIZE, PLANS_TYPE } from '../CONSTANTS';
import i18n from './i18n';

const MAIL_PLANS = ['free', 'plus', 'professional', 'visionary'];
const VPN_PLANS = ['free', 'vpnbasic', 'vpnplus', 'visionary'];

const loadFormat = (I18N, type) => {
    const msgPerDay = (total) => `${total} ${I18N.MSG_PER_DAY}`;
    const numberLabels = (total) => `${total} ${I18N.LABELS}`;

    const formatter = {
        free(plan) {
            plan.sending = msgPerDay(150);
            plan.labels = numberLabels(20);
            plan.support = I18N.LIMITED_SUPPORT;
            plan.order = 1;
            plan.countries = '3';
            plan.protection = 0;
            plan.visionary = 0;
            plan.plusServers = 0;
            plan.speed = I18N.LOW;
            plan.cancel = '--';
            plan.MaxVPN = 1;
            plan.tor = 0;
            plan.tooltipAccess = I18N.TOOLTIP_FREE_ONLY_ACCESS;
            plan.tooltipDevices = I18N.TOOLTIP_DEVICES;
            plan.tooltipPlus = I18N.TOOLTIP_PLUS;
            plan.tooltipCore = I18N.TOOLTIP_SECURE_CORE;
            plan.tooltipTor = I18N.TOOLTIP_TOR;
            plan.tooltipVisionary = I18N.TOOLTIP_VISIONARY;
        },
        plus(plan) {
            plan.sending = msgPerDay(1000);
            plan.labels = numberLabels(200);
            plan.support = I18N.SUPPORT;
        },
        business(plan) {
            plan.sending = '???';
            plan.labels = '???';
            plan.support = '???';
        },
        visionary(plan) {
            plan.sending = I18N.UNLIMITED_SENDING;
            plan.labels = I18N.UNLIMITED_LABELS;
            plan.support = I18N.PRIORITY_SUPPORT;
            plan.order = 4;
            plan.countries = I18N.ALL;
            plan.protection = 1;
            plan.visionary = 1;
            plan.plusServers = 1;
            plan.speed = I18N.HIGHEST;
            plan.cancel = 1;
            plan.tor = 1;
            plan.tooltipAccess = I18N.TOOLTIP_ALL_ACCESS;
            plan.tooltipDevices = I18N.TOOLTIP_DEVICES;
            plan.tooltipPlus = I18N.TOOLTIP_PLUS;
            plan.tooltipCore = I18N.TOOLTIP_SECURE_CORE;
            plan.tooltipTor = I18N.TOOLTIP_TOR;
            plan.tooltipVisionary = I18N.TOOLTIP_VISIONARY;
        },
        vpnbasic(plan) {
            plan.sending = msgPerDay(150);
            plan.labels = numberLabels(20);
            plan.support = I18N.SUPPORT;
            plan.order = 2;
            plan.countries = I18N.ALL;
            plan.protection = 0;
            plan.visionary = 0;
            plan.plusServers = 0;
            plan.speed = I18N.HIGH;
            plan.cancel = 1;
            plan.tor = 0;
            plan.tooltipAccess = I18N.TOOLTIP_ALL_ACCESS;
            plan.tooltipDevices = I18N.TOOLTIP_DEVICES;
            plan.tooltipPlus = I18N.TOOLTIP_PLUS;
            plan.tooltipCore = I18N.TOOLTIP_SECURE_CORE;
            plan.tooltipTor = I18N.TOOLTIP_TOR;
            plan.tooltipVisionary = I18N.TOOLTIP_VISIONARY;
        },
        vpnplus(plan) {
            plan.sending = msgPerDay(150);
            plan.labels = numberLabels(20);
            plan.support = I18N.SUPPORT;
            plan.order = 3;
            plan.countries = I18N.ALL;
            plan.protection = 1;
            plan.visionary = 0;
            plan.plusServers = 1;
            plan.speed = I18N.HIGHEST;
            plan.cancel = 1;
            plan.tor = 1;
            plan.tooltipAccess = I18N.TOOLTIP_ALL_ACCESS;
            plan.tooltipDevices = I18N.TOOLTIP_DEVICES;
            plan.tooltipPlus = I18N.TOOLTIP_PLUS;
            plan.tooltipCore = I18N.TOOLTIP_SECURE_CORE;
            plan.tooltipTor = I18N.TOOLTIP_TOR;
            plan.tooltipVisionary = I18N.TOOLTIP_VISIONARY;
        }
    };
    return formatter;
};

/**
 * Filter plans, Hide others plans form VPN
 * @param  {String} options.Name plan
 * @return {Boolean}
 */
const formatMail = (list = []) => {
    return list.filter(({ Name, Type }) => MAIL_PLANS.includes(Name) || PLANS_TYPE.ADDON);
};

/**
 * Filter plans, Hide others plans: from EMAIL
 * @param  {String} options.Name plan
 * @return {Boolean}
 */
const formatVPN = (list = []) => {
    return list.filter(({ Name, Type }) => VPN_PLANS.includes(Name) || PLANS_TYPE.ADDON);
};

const format = ({ Cycle, Currency }, noFilter) => (plans = []) => {
    const list = plans.slice();
    const { FORMAT } = i18n();

    // Add free plan
    list.unshift({
        ID: `FREE_${Cycle}_${Currency}`,
        Type: 1,
        Cycle,
        Currency,
        Name: 'free',
        Title: 'Free',
        Amount: 0,
        MaxDomains: 0,
        MaxAddresses: 1,
        MaxSpace: 500 * BASE_SIZE * BASE_SIZE,
        MaxMembers: 1,
        TwoFactor: 0
    });

    const MAP = loadFormat(FORMAT);
    list.forEach((plan) => {
        (MAP[plan.Name] || _.identity)(plan);
    });

    if (noFilter) {
        return list;
    }

    if (isVPN()) {
        return formatVPN(list);
    }

    if (isMail()) {
        return formatMail(list);
    }
};

export default format;



