import request from '../utils/request';
import url from '../utils/url';
import { USER_ROLE } from '../CONSTANTS';

const requestURL = url('organizations');

/**
 * Get group info: group name, token IDs,
 * members (ids, names, roles, addresses, used space, space limit),
 * domains (ids, names, verification status for MX, SPF, DKIM),
 * usage and limits (for domains, members, addresses and space),
 * billing info (status, billing cycle, next billing time).
 * Only available for the group admin.
 * @return {Promise}
 */
export const get = async ({ Role }) => {
    if (Role === USER_ROLE.PAID_ADMIN) {
        return request.get(requestURL()).then(({ Organization }) => Organization);
    }

    return {
        Code: 1000,
        Organization: {
            PlanName: 'free',
            MaxMembers: 1,
            HasKeys: 0
        }
    };
};

/**
 * Get organization keys
 * @return {Promise}
 */
export const getKeys = () => request.get(requestURL('keys'));

/**
 * Update organization name
 * @param {Object} params
 * @return {Promise}
 */
export const updateOrganizationName = (params) => request.get(requestURL('name'), { params });

/**
 * Create a new group of given parameters. Requires a subscription.
 * @param {Object} params
 * @return {Promise}
 */
export const create = (params) => request.post(requestURL(), params);



