import appDispatcher from './appDispatcher';

const createMap = (list = []) => list.reduce((acc, key) => ((acc[key] = appDispatcher(key)), acc), {});

export default (list, verbose) => {
    const dispatcher = createMap(list);
    const listeners = [];

    const on = (key) => (type, cb) => listeners.push(dispatcher[type][key](cb, verbose));
    /*
     * we don't want to modify the ref that's the reason why we don't bind it
     * to the dispatcher itself instead of the native onInput
     */
    const onInput = on('onInput', !!verbose);
    const onChange = on('onChange', !!verbose);

    const unsubscribe = () => {
        listeners.forEach((cb) => cb());
        listeners.length = 0;
    };

    return { dispatcher, onInput, onChange, unsubscribe };
};



