import request from '../utils/request';
import url from '../utils/url';
import mailSettingsModel from '../settings/mail';
import settingsI18n from '../settings/i18n';

const requestURL = url('settings/mail');
console.debug('@todo always return MailSettings ?');

const store = (data) => (mailSettingsModel.set('all', data.MailSettings), data);
const req = (type, url, data) => {
    return request[type](requestURL(url), data)
        .then(store)
        .catch((e) => {
            const { MAIL } = settingsI18n();
            if (e.Code === 12010) {
                throw new Error(MAIL.ERROR_SAVE_INPUT);
            }
            throw e;
        });
};

export const fetch = () => req('get').then((data) => data.MailSettings);
export const updateTheme = (data) => req('put', 'theme', data);
export const updateAutoSaveContacts = (data) => req('put', 'autocontacts', data);
export const updateComposerMode = (data) => req('put', 'composermode', data);
