export const updateViewMode = (data) => req('put', 'viewmode', data);
export const updateViewLayout = (data) => req('put', 'viewlayout', data);
export const updateSwipeLeft = (data) => req('put', 'swipeleft', data);
export const updateSwipeRight = (data) => req('put', 'swiperight', data);
export const updateAlsoArchive = (data) => req('put', 'alsoarchive', data);
export const updateHotkeys = (data) => req('put', 'hotkeys', data);
export const updatePMSignature = (data) => req('put', 'pmsignature', data);
export const updateAutowildcard = (data) => req('put', 'autowildcard', data);
export const updateDraftType = (data) => req('put', 'drafttype', data);
export const updateReceiveType = (data) => req('put', 'receivetype', data);
