import request from '../utils/request';
import url from '../utils/url';
import vpnSettingsModel from '../settings/vpn';

const requestURL = url('settings/vpn');
console.debug('@todo always return VPNSettings ?');

const store = (data) => (vpnSettingsModel.set('all', data.VPNSettings), data);
const req = (type, url, data) => request[type](requestURL(url), data).then(store);

export const fetch = () => req('get').then((data) => data.VPNSettings);
export const updateName = (data) => req('put', 'name', data);
export const updatePassword = (data) => req('put', 'password', data);



