import _ from 'lodash';
import appDispatcher from '../utils/appDispatcher';
import * as eventsApi from '../api/eventsApi';

const dispatcher = appDispatcher('events');
const DELAY = 30 * 1000; // every 30 seconds
const CACHE = { events: [] };

/**
 * Add event ID to the cache
 */
const addEventID = ({ EventID }) => CACHE.events.push(EventID);

/**
 * Handle result from the events request and dispatch the events
 * @param  {Object} data Datas receive from the API
 */
function handleEvents(data = {}) {
    dispatcher.change('propagate', data);
    data.EventID && addEventID(data);
}

/**
 * Get information from the last event cached
 * @return {Promise}
 */
export async function call() {
    const data = await eventsApi.get(_.last(CACHE.events));
    return handleEvents(data);
}

/**
 * Get last event ID
 * @return {Promise}
 */
async function getLastEventID() {
    const data = await eventsApi.getLatestID();
    addEventID(data);
    return call();
}

/**
 * Get the last event ID and start interval each 30 seconds to get event information
 */
export async function start() {
    try {
        CACHE.events.length = 0;
        await getLastEventID();
        CACHE.intervalID && clearInterval(CACHE.intervalID);
        CACHE.intervalID = setInterval(call, DELAY);
    } catch (e) {
        console.error(e);
    }
}

/**
 * Stop event interval and clear the events cached
 */
export function stop() {
    CACHE.intervalID && clearInterval(CACHE.intervalID);
    CACHE.events.length = 0;
}



