import { EventEmitter2 } from 'eventemitter2';

import log from './log';

const emitter = new EventEmitter2();
const ACTION_CHANGE = 'CHANGE';
const ACTION_INPUT = 'USER_INPUT';
emitter.setMaxListeners(0);

const proxyLogger = (callback, scope, namespace) => (data = {}) => {
    log(`${scope}.${namespace.slice(0, -1)}`, data.data, data.type);
    callback(data);
};

// Create factory cache
const map = Object.create(null);

const appActionFactory = (name) => {
    const namespace = name ? name + '.' : '';
    const dispatch = ({ type, state = {} }) => emitter.emit(namespace + type, state || {});

    const formatCallback = ({ callback, logger, logger2 }, action) => {
        if (typeof callback === 'string') {
            const tmpCb = ({ type, data }) => type === callback && logger(data);
            return !logger2 ? tmpCb : proxyLogger(tmpCb, action, namespace);
        }
        return !logger ? callback : proxyLogger(callback, action, namespace);
    };

    if (map[name]) {
        return map[name];
    }

    return (map[name] = {
        dispatch,
        dispatchChange(state = {}) {
            dispatch({ type: ACTION_CHANGE, state });
        },
        dispatchInput(state = {}) {
            dispatch({ type: ACTION_INPUT, state });
        },
        changePromise(type = '', reject = true) {
            return (data = {}) => {
                dispatch({ type: ACTION_CHANGE, state: { type, data } });

                // Keep promise status
                if (reject && /\.error$/.test(type)) {
                    return Promise.reject(data);
                }
                return data;
            };
        },

        change(type, data = {}) {
            return this.dispatchChange({ type, data });
        },
        input(type, data = {}) {
            return this.dispatchInput({ type, data });
        },

        /**
         * Subscribe to an action and return its own unsubscribe function
         * @param  {String}   action ACTION_NAME
         * @param  {Function} cb
         * @return {Function}          unsubscribe
         */
        subscribe(action, cb) {
            emitter.on(namespace + action, cb);
            return () => emitter.off(namespace + action, cb);
        },

        /**
         * Subscribe to Action CHANGE
         * @param  {Function} cb or String for filter by type
         * @param  {Boolean} logger log actions or cb if filter by type
         * @param  {Boolean} logger2 to log if filter on
         * @return {Function}          unsubscribe
         */
        onChange(callback, logger = false, logger2 = false) {
            const cb = formatCallback({ callback, logger, logger2 }, ACTION_CHANGE);
            emitter.on(namespace + ACTION_CHANGE, cb);
            return () => emitter.off(namespace + ACTION_CHANGE, cb);
        },
        /**
         * Subscribe to action INPUT
         * @param  {Function} cb
         * @param  {Boolean} logger log actions
         * @return {Function}          unsubscribe
         */
        onInput(callback, logger = false, logger2 = false) {
            const cb = formatCallback({ callback, logger, logger2 }, ACTION_INPUT);
            emitter.on(namespace + ACTION_INPUT, cb);
            return () => emitter.off(namespace + ACTION_INPUT, cb);
        },
        /**
         * Subscribe to an action once, auto destroy after the first call
         * @param  {String}   action ACTION_NAME
         * @param  {Function} cb
         * @return {void}
         */
        subscribeOnce(action, cb) {
            emitter.once(namespace + action, cb);
        }
    });
};

export default (name) => map[name] || appActionFactory(name);



