import {
    addU2FKeyConfirm,
    addU2FKeyRequest,
    disableTOTP as disableTOTPRequest,
    disableTwoFactor as disableTwoFactorRequest,
    regenerateTwoFactorCodes,
    removeU2FKey as removeU2FKeyRequest,
    enableTwoFactor
} from '../api/settingsApi';
import { ucFirstObject } from '../helpers/string';

/**
 * retrieves a U2F register challenge from the API.
 * @param {Boolean} [updateCache=false] - if the request should overpass the cache.
 * @returns {Promise<*>}
 */
export async function getAddU2FChallenge(updateCache = false) {
    return addU2FKeyRequest({ cache: !updateCache });
}

/**
 * sends a U2F register request to the API.
 * @param {Object} data - the register request data.
 * @param {String} data.label - the name of the new key.
 * @param {String} data.keyHandle - data from the U2F Key.
 * @param {String} data.clientData - data from the U2F Key.
 * @param {String} data.registrationData - data from the U2F Key.
 * @param {String} data.version - version used for the registration..
 * @param {Object} creds - the credentials.
 * @param {Object} response - the response for route `auth/info`. If not set, a new call to the route is made.
 * @returns {Promise<*>}
 */
export async function addU2FKey(data, creds, response = undefined) {
    const credentials = ucFirstObject(creds);
    if (credentials.U2F) {
        credentials.U2F = ucFirstObject(credentials.U2F);
    }
    return addU2FKeyConfirm(ucFirstObject(data), credentials, response);
}

/**
 * sends a reset request for the two factor recovery codes.
 * @param {Object} creds - scope credential.
 * @param {?Object} response - the response of `POST /auth/info`. This is useful for U2F.
 * @returns {Promise<*>}
 */
export async function resetRecoveryCodes(creds, response = undefined) {
    const credentials = ucFirstObject(creds);
    if (credentials.U2F) {
        credentials.U2F = ucFirstObject(credentials.U2F);
    }
    return regenerateTwoFactorCodes(credentials, response);
}

/**
 * post the new TOTP data.
 * @param {Object} data - the new data
 * @param {string} data.TOTPSharedSecret - the new shared secret.
 * @param {string} data.TOTPConfirmation - the confirmation code.
 * @param {Object} creds - scope credential.
 * @param {?Object} response - the response of `POST /auth/info`. This is useful for U2F.
 * @returns {Promise<*>}
 */
export async function enableTOTP(data, creds, response = undefined) {
    const credentials = ucFirstObject(creds);
    if (credentials.U2F) {
        credentials.U2F = ucFirstObject(credentials.U2F);
    }
    return enableTwoFactor(data, credentials, response);
}

/**
 * removes a single U2F key from the server.
 * @param {String} keyHandle - The key handle of the U2F Key to remove;
 * @param {Object} creds - The scope credentials.
 * @param {Object} response - the response for route `auth/info`. If not set, a new call to the route is made.
 * @returns {Promise<*>}
 */
export async function removeU2FKey(keyHandle, creds, response) {
    const credentials = ucFirstObject(creds);
    if (credentials.U2F) {
        credentials.U2F = ucFirstObject(credentials.U2F);
    }
    return removeU2FKeyRequest(keyHandle, credentials, response);
}

/**
 * Disables two factor.
 * @param {Object} creds - credentials for the account.
 * @param {Object} response - the response for route `auth/info`. If not set, a new call to the route is made.
 * @returns {Promise<*>}
 */
export async function disableTwoFactor(creds, response) {
    const credentials = ucFirstObject(creds);
    if (credentials.U2F) {
        credentials.U2F = ucFirstObject(credentials.U2F);
    }
    return disableTwoFactorRequest({}, credentials, response);
}

/**
 * Disables TOTP.
 * @param {Object} creds - credentials for the account.
 * @param {Object} response - the response for route `auth/info`. If not set, a new call to the route is made.
 * @returns {Promise<*>}
 */
export async function disableTOTP(creds, response) {
    const credentials = ucFirstObject(creds);
    if (credentials.U2F) {
        credentials.U2F = ucFirstObject(credentials.U2F);
    }
    return disableTOTPRequest({}, credentials, response);
}



