import pmcw from './pmcw';
import { BigNumber, Modulus } from 'asmcrypto.js';

import * as authApi from '../api/authApi';
import appProvider from '../appProvider';
import webcrypto from './webcrypto';
import request from '../utils/request';
import { expandHash, cleanUsername, hashPassword, CURRENT_AUTH_VERSION } from './passwords';

const toNumber = (len) => {
    function toBN(arr) {
        const reversed = new Uint8Array(arr.length);
        for (let i = 0; i < arr.length; i++) {
            reversed[arr.length - i - 1] = arr[i];
        }
        return new BigNumber(reversed);
    }

    function fromBN(bn) {
        const arr = bn.toBytes();
        const reversed = new Uint8Array(len / 8);
        for (let i = 0; i < arr.length; i++) {
            reversed[arr.length - i - 1] = arr[i];
        }
        return reversed;
    }

    return { fromBN, toBN };
};

const makeRequest = (type) => (url, config, { headers = {}, creds = {}, response } = {}) => {
    return performSRPRequest({ type, url, config, creds, response, headers });
};

const requestMap = {
    put: makeRequest('put'),
    post: makeRequest('post'),
    delete: makeRequest('delete'),
    get: makeRequest('get')
};

export default requestMap;

/**
 * [generateProofs description]
 * @param  {Integer} len            Size of the proof (bytes length)
 * @param  {Function} hash          Create a hash Function:<Uint8Array>
 * @param  {Uint8Array} modulus
 * @param  {Uint8Array} hashedPassword
 * @param  {Uint8Array} serverEphemeral
 * @return {Object}
 */
function generateProofs({ len, hash, modulus, hashedPassword, serverEphemeral }) {
    const generator = new BigNumber(2);
    const { fromBN, toBN } = toNumber(len);

    let multiplier = toBN(hash(openpgp.util.concatUint8Array([fromBN(generator), modulus])));

    modulus = toBN(modulus);
    serverEphemeral = toBN(serverEphemeral);
    hashedPassword = toBN(hashedPassword);

    const modulusMinusOne = modulus.subtract(1);

    if (modulus.bitLength !== len) {
        throw new Error('SRP modulus has incorrect size');
    }

    modulus = new Modulus(modulus);
    multiplier = modulus.reduce(multiplier);

    if (multiplier.compare(1) <= 0 || multiplier.compare(modulusMinusOne) >= 0) {
        throw new Error('SRP multiplier is out of bounds');
    }

    if (generator.compare(1) <= 0 || generator.compare(modulusMinusOne) >= 0) {
        throw new Error('SRP generator is out of bounds');
    }

    if (serverEphemeral.compare(1) <= 0 || serverEphemeral.compare(modulusMinusOne) >= 0) {
        throw new Error('SRP server ephemeral is out of bounds');
    }

    let clientSecret, clientEphemeral, scramblingParam;
    const serverEphemeralRaw = fromBN(serverEphemeral);
    do {
        do {
            clientSecret = toBN(webcrypto.getRandomValues(new Uint8Array(len / 8)));
        } while (clientSecret.compare(len * 2) <= 0); // Very unlikely

        clientEphemeral = modulus.power(generator, clientSecret);
        scramblingParam = toBN(hash(openpgp.util.concatUint8Array([fromBN(clientEphemeral), serverEphemeralRaw])));
    } while (scramblingParam.compare(0) === 0); // Very unlikely

    let subtracted = serverEphemeral.subtract(
        modulus.reduce(modulus.power(generator, hashedPassword).multiply(multiplier))
    );

    if (subtracted.compare(0) < 0) {
        subtracted = subtracted.add(modulus);
    }
    const exponent = scramblingParam
        .multiply(hashedPassword)
        .add(clientSecret)
        .divide(modulus.subtract(1)).remainder;

    const ClientEphemeral = fromBN(clientEphemeral);
    const sharedSession = fromBN(modulus.power(subtracted, exponent));
    const ClientProof = hash(openpgp.util.concatUint8Array([ClientEphemeral, serverEphemeralRaw, sharedSession]));
    const ExpectedServerProof = hash(openpgp.util.concatUint8Array([ClientEphemeral, ClientProof, sharedSession]));

    return { ClientEphemeral, ExpectedServerProof, ClientProof };
}

function generateVerifier(len, hashedPassword, modulus) {
    const generator = new BigNumber(2);
    const { fromBN, toBN } = toNumber(len);
    modulus = new Modulus(toBN(modulus));
    hashedPassword = toBN(hashedPassword);
    const verifier = modulus.power(generator, hashedPassword);
    return fromBN(verifier);
}

async function tryRequest(config) {
    const response = await authInfo(config.creds.Username);
    return tryAuth({ ...config, response });
}

const srpHasher = (arr) => expandHash(pmcw.arrayToBinaryString(arr));

async function tryAuth({ type, url, config, creds, headers, fallbackAuthVersion, response }) {
    const session = response.SRPSession;
    const modulus = pmcw.binaryStringToArray(
        pmcw.decode_base64(openpgp.cleartext.readArmored(response.Modulus).getText())
    );
    const serverEphemeral = pmcw.binaryStringToArray(pmcw.decode_base64(response.ServerEphemeral));

    let authVersion = response.Version;
    const usedFallback = authVersion === 0;
    if (usedFallback) {
        authVersion = fallbackAuthVersion;
    }

    if (authVersion < 3) {
        creds.Username = response.Username;
        // See https://github.com/ProtonMail/Angular/issues/4332
        // throw new Error('An unexpected error has occurred. Please log out and log back in to fix it.');
    }

    if (
        (authVersion === 2 && cleanUsername(creds.Username) !== cleanUsername(response.Username)) ||
        (authVersion <= 1 && creds.Username.toLowerCase() !== response.Username.toLowerCase())
    ) {
        throw new Error('Please login with just your ProtonMail username (without @protonmail.com or @protonmail.ch).');
    }

    let salt = '';
    if (authVersion >= 3) {
        salt = pmcw.decode_base64(response.Salt);
    }

    try {
        const hashed = await hashPassword(creds, { salt, modulus, authVersion });

        const { ClientEphemeral, ClientProof, ExpectedServerProof } = generateProofs({
            len: 2048,
            hash: srpHasher,
            modulus,
            hashedPassword: hashed,
            serverEphemeral
        });

        const opt = {
            ...config,
            SRPSession: session,
            ClientEphemeral: pmcw.encode_base64(pmcw.arrayToBinaryString(ClientEphemeral)),
            ClientProof: pmcw.encode_base64(pmcw.arrayToBinaryString(ClientProof)),
            TwoFactorCode: creds.TwoFactorCode,
            U2F: creds.U2F
        };
        const data = await request[type](url, opt, headers);

        if (pmcw.encode_base64(pmcw.arrayToBinaryString(ExpectedServerProof)) === data.ServerProof) {
            return { data, authVersion };
        }
    } catch (e) {
        e.usedFallback = usedFallback;
        throw e;
    }
}

export async function randomVerifier(Password) {
    const data = await authApi.modulus();
    const modulus = pmcw.binaryStringToArray(pmcw.decode_base64(openpgp.cleartext.readArmored(data.Modulus).getText()));
    const salt = pmcw.arrayToBinaryString(webcrypto.getRandomValues(new Uint8Array(10)));
    const hashedPassword = await hashPassword({ Password }, { salt, modulus, authVersion: CURRENT_AUTH_VERSION });

    const verifier = generateVerifier(2048, hashedPassword, modulus);
    return {
        Auth: {
            Version: CURRENT_AUTH_VERSION,
            ModulusID: data.ModulusID,
            Salt: pmcw.encode_base64(salt),
            Verifier: pmcw.encode_base64(pmcw.arrayToBinaryString(verifier))
        }
    };
}

export function authInfo(Username) {
    return authApi.info({
        Username,
        ClientID: appProvider.getConfig('clientID'),
        ClientSecret: appProvider.getConfig('clientSecret')
    });
}

/**
 * Check the validity of a user
 * @param  {String} method          HTTP methods
 * @param  {String} endpoint        URL
 * @param  {Object} req             {Username:<String>, ClientID:<String>, ClientSecret:<String>}
 * @param  {Object} creds           {Username:<String>, Password:<String>, TwoFactorCode:<Null>}
 * @param  {void} initialInfoResp
 * @param  {Object} headers
 * @return {Promise}
 */
export function performSRPRequest(config) {
    const opt = { fallbackAuthVersion: 2, ...config };
    const { creds } = config;
    const promise = config.response ? tryAuth(opt) : tryRequest(opt);

    return promise
        .catch((err) => {
            if (err.usedFallback === true && cleanUsername(creds.Username) !== creds.Username.toLowerCase()) {
                return tryRequest({ fallbackAuthVersion: 1, ...config });
            }
            throw err;
        })
        .catch((err) => {
            if (err.usedFallback === true) {
                return tryRequest({ fallbackAuthVersion: 0, ...config });
            }
            throw err;
        });
}

/**
 * Get the configuration available for a password, and extend your config with it
 * @param  {String} password
 * @param  {Object} config
 * @return {Promise}
 */
export async function getPasswordParams(password, config = {}) {
    const data = await randomVerifier(password);
    return { ...config, ...data };
}



