import { USER_STATUS } from '../CONSTANTS';
import authI18n from './i18n';
import * as authUserProcess from './authProcess';
import userModel from '../user/model';
import { clear as clearSubscription, fetch as loadSubscription } from '../subscription/model';
import * as eventManager from '../utils/eventManager';
import * as i18nLoader from '../utils/i18nLoader';
import appDispatcher from '../utils/appDispatcher';
import appProvider from '../appProvider';
import { isAdmin, isVPN } from '../utils/appType';

const dispatcher = appDispatcher('auth');
let ghostAuth;

function checkVPNisActive(user) {
    if (user.VPN.Status === USER_STATUS.VPN_NO || user.VPN.Status === USER_STATUS.VPN_REQUESTED) {
        logout();
        dispatcher.change('login.vpnuser.error', user);
        return false;
    }

    dispatcher.change('login.vpnuser.success', user);
    return true;
}

export async function login({ username, password }) {
    ghostAuth = { username, password };
    const config = await authUserProcess.login(username, password);
    if (!config.is2FA && !config.isUnlockable) {
        return processLogin();
    }

    // VPN account is unlockable post signup
    if (!config.is2FA && config.isUnlockable && isVPN()) {
        return processLogin();
    }

    return { config };
}

export async function login2FA({ password2FA, U2FResponse }) {
    const config = await authUserProcess.login2FA(ghostAuth.username, ghostAuth.password, { password2FA, U2FResponse });
    if (!config.isUnlockable) {
        return unlock({ passwordUnlock: ghostAuth.password });
    }

    return { config };
}

export async function unlock({ passwordUnlock }) {
    // If 2FA admin, we don't need to unlock the user
    if (isAdmin()) {
        return processLogin();
    }

    return authUserProcess.unlock(passwordUnlock).then(processLogin);
}

/**
 * Last step of the auth process,
 * fetch informations about the user
 * then it tells to the application, we're done.
 */
async function prepareUser() {
    // Admin app cannot access to these routes.
    if (isAdmin()) {
        return { user: {}, subscription: {} };
    }

    if (appProvider.getConfig('loadI18n') !== false) {
        await appProvider.loadI18n(i18nLoader.loadLocale());
    }

    const { USER_CONNECTOR } = authI18n();
    const user = await userModel.load();
    if (isVPN()) {
        if (!checkVPNisActive(user)) {
            throw new Error(USER_CONNECTOR.VPN_ERROR);
        }
    }
    eventManager.start(); // no need to wait for it

    if (!isVPN() && !user.Keys.length) {
        return { user, config: { noKeys: true } };
    }

    const subscription = await loadSubscription();
    return { subscription, user };
}

/**
 * Login flow for a user,
 * we need to dispatch the event after user, subscription and the event.
 * Why ? To prevent errors, if the user try to logged out
 */
async function processLogin() {
    try {
        const data = await prepareUser();
        dispatcher.change('login.success', data);
        ghostAuth = undefined;
        return data;
    } catch (e) {
        dispatcher.change('login.error', e);
        throw e;
    }
}

/**
 * Login flow for a user,
 * we need to dispatch the event after user, subscription and the event.
 * Why ? To prevent errors, if the user try to logged out
 */
export async function reloadAuth() {
    try {
        if (!userModel.isLoggedIn()) {
            return {};
        }
        const data = await prepareUser();
        dispatcher.change('login.reload.success', data);
        ghostAuth = undefined;
        return data;
    } catch (e) {
        dispatcher.change('login.reload.error', e);
        throw e;
    }
}

export async function logout() {
    try {
        eventManager.stop();
        const data = await authUserProcess.logout();
        userModel.clear(null, true);
        clearSubscription();
        dispatcher.change('logout.success', data);
        return data;
    } catch (e) {
        dispatcher.change('logout.error', e);
        throw e;
    }
}

export const eligible = logout;

export default { login, login2FA, unlock, reloadAuth, logout, eligible };



