import { c, t, ngettext, msgid } from 'c-3po';

export default function load() {
    const FORMAT = {
        LABELS: c('Plan details').t`Labels`,
        SUPPORT: c('Plan details').t`Support`,
        MSG_PER_DAY: c('Plan details').t`Messages per day`,
        UNLIMITED_LABELS: c('Plan details').t`Unlimited labels`,
        LIMITED_SUPPORT: c('Plan details').t`Limited support`,
        PRIORITY_SUPPORT: c('Plan details').t`Priority support`,
        UNLIMITED_SENDING: c('Plan details').t`Unlimited sending`,
        ALL: c('Plan details').t`All`,
        LOW: c('Plan details').t`Low`,
        HIGH: c('Plan details').t`High`,
        HIGHEST: c('Plan details').t`Highest`,
        TOOLTIP_DEVICES: c('Plan details').t`Number of simultaneous connections`,
        TOOLTIP_ALL_ACCESS: c('Plan details').t`Access to VPN server in all countries`,
        TOOLTIP_FREE_ONLY_ACCESS: c('Plan details').t`Access to VPN server in 3 countries`,
        TOOLTIP_PLUS: c('Plan details')
            .t`Exclusive servers only available to Plus and Visionary users with higher speed`,
        TOOLTIP_SECURE_CORE: c('Plan details')
            .t`Provides additional protection against VPN server compromise by routing connections through our Secure Core network`,
        TOOLTIP_TOR: c('Plan details').t`Send all your traffic through the Tor network with a single click`,
        TOOLTIP_VISIONARY: c('Plan details').t`Includes ProtonMail encrypted email with all features`
    };

    const PLANS = {
        VPN: c('Plan details').t`connections`,
        ADDRESSES: c('Plan details').t`addresses`,
        domain(value) {
            return c('Plan details').ngettext(msgid`domain`, `domains`, value);
        },
        member(value) {
            return c('Plan details').ngettext(msgid`member`, `members`, value);
        }
    };

    return { FORMAT, PLANS };
}



