import bcryptJS from 'bcryptjs';
import pmcw from './pmcw';
import webcrypto from './webcrypto';

export const CURRENT_AUTH_VERSION = 4;
export const cleanUsername = (name = '') => name.replace(/\.|-|_/g, '').toLowerCase();

export function bcrypt(str, salt) {
    return new Promise((resolve, reject) => {
        bcryptJS.hash(str, salt, (err, hash) => {
            if (typeof hash !== 'string') {
                return reject(err);
            }
            resolve(hash);
        });
    });
}

export function expandHash(str) {
    return openpgp.util.concatUint8Array([
        openpgp.crypto.hash.sha512(pmcw.binaryStringToArray(str + '\x00')),
        openpgp.crypto.hash.sha512(pmcw.binaryStringToArray(str + '\x01')),
        openpgp.crypto.hash.sha512(pmcw.binaryStringToArray(str + '\x02')),
        openpgp.crypto.hash.sha512(pmcw.binaryStringToArray(str + '\x03'))
    ]);
}

export async function computeKeyPassword(password, salt) {
    if (salt && salt.length) {
        const saltBinary = pmcw.binaryStringToArray(pmcw.decode_base64(salt));
        const hash = await bcrypt(password, '$2y$10$' + bcryptJS.encodeBase64(saltBinary, 16));
        // Remove bcrypt prefix and salt (first 29 characters)
        return hash.slice(29);
    }
    // No salt, old-style
    return password;
}

export function generateKeySalt() {
    return pmcw.encode_base64(pmcw.arrayToBinaryString(webcrypto.getRandomValues(new Uint8Array(16))));
}

const hashPasswordVersion = {
    4(password, salt, modulus) {
        return hashPasswordVersion[3](password, salt, modulus);
    },

    3(password, salt, modulus) {
        const saltBinary = pmcw.binaryStringToArray(salt + 'proton');
        // We use the latest version of bcrypt, 2y, with 2^10 rounds.
        return bcrypt(password, '$2y$10$' + bcryptJS.encodeBase64(saltBinary, 16)).then((unexpandedHash) => {
            return expandHash(unexpandedHash + pmcw.arrayToBinaryString(modulus));
        });
    },

    2(password, userName, modulus) {
        return hashPasswordVersion[1](password, cleanUsername(userName), modulus);
    },

    1(password, userName, modulus) {
        const salt = openpgp.crypto.hash.md5(pmcw.binaryStringToArray(pmcw.encode_utf8(userName.toLowerCase())));
        let encodedSalt = '';
        for (let i = 0; i < salt.length; i++) {
            let byte = salt[i].toString(16);
            if (byte.length === 1) {
                byte = '0' + byte;
            }
            encodedSalt += byte;
        }
        // See hash version 3 for explanation of the prefix
        return bcrypt(password, '$2y$10$' + encodedSalt).then((unexpandedHash) => {
            return expandHash(unexpandedHash + pmcw.arrayToBinaryString(modulus));
        });
    },

    0(password, userName, modulus) {
        const prehashed = pmcw.encode_base64(
            pmcw.arrayToBinaryString(
                openpgp.crypto.hash.sha512(
                    pmcw.binaryStringToArray(userName.toLowerCase() + pmcw.encode_utf8(password))
                )
            )
        );
        return hashPasswordVersion[1](prehashed, userName, modulus);
    }
};

export async function hashPassword({ Username, Password }, { authVersion, salt, modulus }) {
    if (authVersion === 3 || authVersion === 4) {
        return hashPasswordVersion[authVersion](Password, salt, modulus);
    }
    if (authVersion >= 0 && authVersion <= 2) {
        return hashPasswordVersion[authVersion](Password, Username, modulus);
    }

    throw new Error('Unsupported auth version');
}



