import { omit } from 'lodash';
import { setItem } from '../crypto/secureSessionStorage';

const CACHE = {
    headersSet: false,
    model: {}
};

export const save = (data = {}) => (CACHE.model = { ...CACHE.model, ...data });
export const read = (key = '') => CACHE.model[key];
export const get = (key = '') => CACHE[key];
export const set = (key = '', value) => (CACHE[key] = value);
export const clear = () => {
    CACHE.model = {};
    CACHE.headersSet = false;
};

/**
 * Store auth session informations
 * @param  {Object} data
 * @return {void}
 */
export function storeData(data = {}) {
    setItem('UID', data.UID);
    save(omit(data, 'Code', 'ServerProof'));
}



