import { setItem, removeItem, getItem } from '../utils/tempStorage';
import { passwordUpgrade } from '../api/settingsApi';

const key = 'auth';
const tenMinutes = 10 * 60 * 1000; // 10 minutes

/**
 * Store standard Auth object
 * @param {Object} value - Auth object
 */
export function store(value) {
    setItem(key, value, tenMinutes);
}

/**
 * Send upgrade credentials
 * @return {Promise}
 */
export default async function send() {
    const Auth = getItem(key);
    if (Auth) {
        removeItem(key);
        return passwordUpgrade(Auth);
    }
}



