import appDispatcher from '../utils/appDispatcher';
const dispatcher = appDispatcher('events');

export default (key) => {
    let CACHE = {};
    const get = (key = 'all') => ({ ...(key === 'all' ? CACHE : CACHE[key]) });
    const clear = () => (CACHE = {});
    const set = (key = 'all', value) => {
        if (key === 'all') {
            return (CACHE = { ...CACHE, ...value });
        }
        CACHE[key] = value;
    };

    dispatcher.onChange(
        'propagate',
        (data) => {
            data[key] && set('all', data[key]);
        },
        true
    );

    return { set, get, clear };
};



