import _ from 'lodash';

import dispatchers from '../utils/dispatchers';
import { DEFAULT_CYCLE, DEFAULT_CURRENCY } from '../CONSTANTS';
import userModel from '../user/model';
import * as paymentsApi from '../api/paymentsApi';

const CACHE = {};
const OPT = {
    VPN_BASIC: ['vpnbasic'],
    VPN_PLUS: ['vpnplus'],
    VISIONARY: ['visionary'],
    PAID_VPN: ['vpnbasic', 'vpnplus', 'visionary'],
    PAID_MAIL: ['plus', 'professional', 'visionary']
};

const { dispatcher } = dispatchers(['subscription', 'user', 'auth']);

export const get = () => ({ ...CACHE.subscription });
export const clear = () => Object.keys(CACHE).forEach((key) => delete CACHE[key]);
const set = (subscription = {}, noEvent = false) => {
    console.debug('@todo remove when the API is ready');
    const PlanIDs = _.countBy(subscription.Plans || [], ({ ID }) => ID);
    subscription.Plans = subscription.Plans.map((plan) => {
        plan.Quantity = PlanIDs[plan.ID];
        return plan;
    });
    CACHE.subscription = { ...subscription };
    !noEvent && dispatcher.subscription.change('update', { subscription });
};

function currency() {
    const { Plans = [] } = CACHE.subscription || {};
    const [{ Currency = DEFAULT_CURRENCY } = {}] = Plans;
    return Currency;
}

function cycle() {
    const { Plans = [] } = CACHE.subscription || {};
    const [{ Cycle = DEFAULT_CYCLE } = {}] = Plans;
    return Cycle;
}

const hasFactory = (plans = []) => () => {
    const { Plans = [] } = CACHE.subscription || {};
    return Plans.some(({ Name }) => plans.indexOf(Name) > -1);
};

const hasVPNBasic = hasFactory(OPT.VPN_BASIC);
const hasVPNPlus = hasFactory(OPT.VPN_PLUS);
const hasVisionary = hasFactory(OPT.VISIONARY);

export async function fetch() {
    if (!userModel.isAdmin()) {
        return {};
    }

    return paymentsApi
        .subscription()
        .then((subscription) => (set(subscription), subscription))
        .then(dispatcher.subscription.changePromise('load.success'))
        .catch(dispatcher.subscription.changePromise('load.error'));
}

export async function valid({ plans = [], CouponCode, Currency, Cycle }) {
    const PlanIDs = _.countBy(plans, 'ID');
    const check = await paymentsApi.valid({ CouponCode, PlanIDs, Currency, Cycle });
    return { check, plans };
}

/**
 * If you don't specify Amount, it's set to 0 which means:
 *  => paid before subscription
 * @return {Promise}
 */
async function subscribe({ Currency, PlanIDs = [], Amount = 0, Payment: pay, PaymentMethodID, Cycle }) {
    const data = await paymentsApi.subscribe({ Amount, Currency, PaymentMethodID, PlanIDs, Payment: pay, Cycle });
    set(data.Subscription);
    return data;
}

dispatcher.user.onChange(({ type }) => {
    type === 'disable.success' && fetch();
});

dispatcher.subscription.onChange(({ type }) => {
    type === 'status.update' && fetch();
});



