import appProvider from '../appProvider';

// Admin doesn't use the same format (we should use the same as it's better)
const getType = () => appProvider.getConfig('clientID') || appProvider.getConfig('CLIENT_ID');
const is = (type) => () => getType() === type;

export const isMail = is('Web');
export const isVPN = is('WebVPN');
export const isAdmin = is('Admin');
export const label = () => {
    const name = isMail() ? 'mail' : 'vpn';
    return isAdmin() ? 'admin' : name;
};



