import { c, t } from 'c-3po';

export default function load() {
    const USER_CONNECTOR = {
        VPN_ERROR: c('Error').t`Cannot access to the VPN dashboard`,
        NO_KEYS: c('Error').t`No keys available for the user`
    };
    const AUTH_PROCESS = {
        ERROR_2FA_TOTP_INPUT: c('Error').t`Please enter your two-factor passcode`,
        ERROR_2FA_U2F_INPUT: c('Error').t`Please activate your security token`,
        ERROR_INPUT_VALIDATION: c('Error').t`Please enter your username and password`,
        ERROR_PASSWORD_MISSING: c('Error').t`Your password is missing`
    };

    return {
        USER_CONNECTOR,
        AUTH_PROCESS
    };
}



