import { fetch as fetchSettings } from '../api/settingsApi';
import { fetch as fetchVpnSettings } from '../api/settingsVpnApi';
import { fetch as fetchMailSettings } from '../api/settingsMailApi';
import { isMail, isVPN } from '../utils/appType';

/**
 * Load settings for a user based on the type of app
 * @return {Promise}
 */
export default async () => {
    const [user, mail = {}, vpn = {}] = await Promise.all([
        fetchSettings(),
        isMail() ? fetchMailSettings() : Promise.resolve(),
        isVPN() ? fetchVpnSettings() : Promise.resolve()
    ]);
    return { user, mail, vpn };
};



