import { randomString } from '../crypto/utils';
import * as authApi from '../api/authApi';
import { storeData } from './authModel';
import { save } from './cache';
import appProvider from '../appProvider';

export async function setAuthCookie(config = {}) {
    console.debug('@todo use constants for the error');
    try {
        const { RefreshToken, UID: Uid } = config;
        const data = await authApi.cookies({
            ResponseType: 'token',
            ClientID: appProvider.getConfig('clientID'),
            GrantType: 'refresh_token',
            Uid,
            RefreshToken,
            RedirectURI: appProvider.getConfig('url'),
            State: randomString(24)
        });
        storeData(data);
        return config;
    } catch (e) {
        console.error(e);
        const { data = {} } = e;
        const link = `<a target="_blank" href="${appProvider.getConfig('links.cookies')}">Learn more</a>`;
        throw new Error(`Error setting authentication cookies. ${link}`);
    }
}

/**
 * Refresh the cookie and store the information into the cache
 * @return {Promise}
 */
export async function refreshCookie() {
    try {
        const data = await authApi.refresh();
        save(data);
    } catch (e) {
        console.error(e);
        const { data = {} } = e;
        throw new Error(data.ErrorDescription || data.Error);
    }
}



