import request from '../../../utils/request';
import { logout } from '../../../auth/userConnector';
import { hasExistingSession } from '../../../user/model';
import { refreshCookie } from '../../../auth/cookiesModel';

const PROMISES = [];

const clear = () => (PROMISES.length = 0);
const fail = () => (clear(), logout());

const recall = (config) => {
    /**
     * @todo  check if it works. It should.
     * As the header won't exist anymore, maybe axios will auto extend it.
     */
    delete config.config.headers['x-pm-session'];
    return request(config);
};

export const onError = (error) => {
    if (!hasExistingSession()) {
        return fail();
    }

    const isRefreshing = PROMISES.length;

    if (!isRefreshing) {
        PROMISES.push(error);

        return refreshCookie()
            .then(() => Promise.all(PROMISES.map(recall)))
            .catch(fail);
    }

    PROMISES.push(error);
    return Promise.reject(error);
};



