import * as userApi from '../api/userApi';
import * as paymentsApi from '../api/paymentsApi';
import settingsLoader from '../settings/loader';
import * as organizationApi from '../api/organizationApi';
import { MAILBOX_PASSWORD_KEY, USER_ROLE, DEFAULT_CURRENCY } from '../CONSTANTS';
import { setItem, getItem } from '../crypto/secureSessionStorage';
import * as authModel from '../auth/authModel';
import pmcw from '../crypto/pmcw';

const MODEL = { keys: {} };
const save = (key, data) => (MODEL[key] = data);
export const read = (key = '') => MODEL[key];
export const get = () => read('user') || {};
const clear = (key, all = false) => {
    if (all) {
        return Object.keys(MODEL).forEach((key) => delete MODEL[key]);
    }
    delete MODEL[key];
};

export const load = async () => {
    const data = await userApi.get();
    extend(data.User);
    return data.User;
};

export const hasExistingSession = () => !!authModel.read('UID');

export const loadExtendedConfig = async (user = {}) => {
    const [orgaData, paymentStatus, paymentMethod, settings] = await Promise.all([
        organizationApi.get(user),
        paymentsApi.status(),
        paymentsApi.methods(),
        settingsLoader()
    ]);

    save('paymentMethod', paymentMethod);

    return {
        settings,
        organization: orgaData.Organization,
        payment: {
            status: paymentStatus,
            method: paymentMethod
        }
    };
};

export function getPassword() {
    const value = getItem(MAILBOX_PASSWORD_KEY);
    return value ? pmcw.decode_utf8_base64(value) : undefined;
}

function savePassword(pwd) {
    // Never save mailbox password changes if sub user
    if (read('user') && read('user').OrganizationPrivateKey) {
        return;
    }

    setItem(MAILBOX_PASSWORD_KEY, pmcw.encode_utf8_base64(pwd));
}

/**
 * Extend default cache of a user to extend the model
 * @param  {Object} opt
 * @return {void}
 */
function extend(opt = {}) {
    save('user', { ...read('user'), ...opt });
}

/**
 * Check if the user is already logged in
 * @return {Boolean}
 */
export function isLoggedIn() {
    authModel.detectAuthenticationState();
    const loggedIn = hasExistingSession();
    if (loggedIn && !authModel.isHeadersSet()) {
        authModel.setAuthHeaders();
    }
    return loggedIn;
}

const isAdmin = (user = get()) => user.Role === USER_ROLE.PAID_ADMIN;
const isMember = (user = get()) => user.Role === USER_ROLE.PAID_MEMBER;
const hasTwoFactor = (user = get()) => user.TwoFactor === 1;
const isSubUser = (user = get()) => typeof user.OrganizationPrivateKey !== 'undefined';
const hasVpn = (user = get()) => user.VPN.Status === 1;
const isTrial = (user = get()) => (user.VPN || {}).PlanName === 'trial';
export const hasMail = (user = get()) => (user.Addresses || []).length > 0;
export const isFree = (user = get()) => !user.Subscribed;
const isPaid = (user = get()) => user.Subscribed;
const hasPaidMail = (user = get()) => user.Subscribed & 1;
const hasPaidVpn = (user = get()) => user.Subscribed & 4;
const isLoaded = () => !!read('user');
const currency = (user = get()) => user.Currency || DEFAULT_CURRENCY;
const hasTrialVpn = ({ VPN = {} } = get()) => VPN.PlanName === 'trial';
// Whether the mailbox' password is accessible, or if the user needs to re-enter it
const isLocked = () => isLoggedIn() === false || typeof getPassword() === 'undefined';
const isSecured = () => isLoggedIn() && typeof getPassword() !== 'undefined';

export default {
    save,
    read,
    clear,
    load,
    get,
    extend,
    isFree,
    isPaid,
    hasPaidMail,
    hasPaidVpn,
    hasTrialVpn,
    isLoggedIn,
    isLocked,
    isSecured,
    isLoaded,
    isAdmin,
    isMember,
    isSubUser,
    hasTwoFactor,
    hasVpn,
    hasMail,
    isTrial,
    getPassword,
    savePassword,
    currency
};



