import request from '../utils/request';
import url from '../utils/url';
import { getPasswordParams } from '../crypto/srp';

const requestURL = url('users');

export const get = (data) => request.get(requestURL());
export const code = (data) => request.post(requestURL('code'), data);
export const create = async (password, params) => {
    const data = await getPasswordParams(password, params);
    return request.post(requestURL(), data);
};



