import request from '../utils/request';
import url from '../utils/url';
import * as handle401 from './interceptors/auth/handle401';

request.interceptors.request.use(null, handle401.onError);

const requestURL = url('auth');

/**
 * Set up SRP authentication request
 * @return {Promise}
 */
export const info = (opt = {}) => request.post(requestURL('info'), opt);
export const modulus = () => request.get(requestURL('modulus'));

export const revoke = () => request.delete(requestURL());
export const refresh = (opt = {}) => request.post(requestURL('refresh'), opt);
export const cookies = (opt = {}) => request.post(requestURL('cookies'), opt);



