import i18n from './i18n';
import * as authModel from './authModel';
import appDispatcher from '../utils/appDispatcher';
import { clear as clearTempStorage } from '../utils/tempStorage';
import { setAuthCookie } from './cookiesModel';
import pmcw from '../crypto/pmcw';
import srp, { getPasswordParams, authInfo } from '../crypto/srp';
import url from '../utils/url';
import { computeKeyPassword, CURRENT_AUTH_VERSION } from '../crypto/passwords';
import upgradePassword, { store as storePassword } from '../crypto/upgradePassword';
import { isAdmin } from '../utils/appType';
import * as authApi from '../api/authApi';
import userModel from '../user/model';
import appProvider from '../appProvider';
import { ucFirstObject } from '../helpers/string';

const requestURL = url('auth');
const UNLOCK_SESSION = 2;
const CACHE = {
    SRP: {},
    CREDENTIALS: {}
};

const dispatcher = appDispatcher('auth');

const clear = () => (delete CACHE.SRP, delete CACHE.CREDENTIALS);
const get = (key = 'CREDENTIALS') => CACHE[key];
const set = (key = 'CREDENTIALS', data = {}) => {
    !CACHE[key] && (CACHE[key] = {});
    CACHE[key] = { ...CACHE[key], ...data };
};

/**
 * Connect a user ~ step 1
 * @param  {String} Username
 * @param  {String} Password      [description]
 * @param  {String} TwoFactorCode [description]
 * @param  {Object} U2FResponse
 * @return {Promise}
 */
async function loginUser(Username, Password, TwoFactorCode = '', U2FResponse = {}) {
    const config = {
        Username,
        ClientID: appProvider.getConfig('clientID'),
        ClientSecret: appProvider.getConfig('clientSecret')
    };
    const { data, authVersion } = await srp.post(requestURL(), config, {
        creds: { Username, Password, TwoFactorCode, U2F: { ...U2FResponse } },
        response: get('SRP')
    });

    // Upgrade users to the newest auth version
    if (authVersion < CURRENT_AUTH_VERSION) {
        const pwds = await getPasswordParams(Password);
        storePassword(pwds);
    }

    /*
        this is a trick! we dont know if we should go to unlock or step2
        because we dont have user's data yet.
        So we redirect to the login page (current page), and this is determined in the resolve:
            - promise on that state in the route.
        this is because we dont want to do another fetch info here.
     */
    return data;
}

/**
 * Connect a user ~ Step 2
 * Redirect the user to the setup (password) or unlock
 * @param  {String} username
 * @param  {String} password
 * @param  {String} TwoFactorCode
 * @param  {Object} U2FResponse
 * @return {Promise}
 */
async function loginProcess(username, password, TwoFactorCode = '', U2FResponse = null) {
    try {
        const data = await loginUser(username, password, TwoFactorCode, U2FResponse);

        if (data.Code === 401) {
            throw new Error(data.ErrorDescription);
        }

        // This account is disabled.
        if (data.Code === 10002) {
            throw new Error(data.Error || 'Your account has been disabled.');
        }
        // Store temp values
        set('CREDENTIALS', {
            username,
            password,
            authResponse: data
        });

        if (typeof data.PrivateKey === 'undefined') {
            authModel.storeData(data);
            return setAuthCookie(data);
        }

        if (!data.AccessToken) {
            throw new Error('Unable to log you in.');
        }
        return data || {};
    } catch (e) {
        if (e.data) {
            const { data } = e;
            throw new Error(
                data.ErrorDescription || data.Error || 'Sorry, our login server is down. Please try again later.'
            );
        }
        throw e;
    }
}

/**
 * Get SRP informations for a user based on the username
 * @param  {String} username
 * @param  {String} password
 * @return {Promise}
 */
async function srpLogin(username, password) {
    const data = await authInfo(username);
    set('SRP', data);
    if (data.TwoFactor === 0) {
        // user does not have two factor enabled, we will proceed to the auth call
        return loginProcess(username, password);
    }
    // user has two factor enabled, they need to enter a code first
    return {
        is2FA: data.TwoFactor !== 0,
        twoFactorData: {
            isTOTP: data['2FA'].TOTP !== 0,
            U2F: data['2FA'].U2F
        }
    };
}

export async function login(username = '', password = '') {
    const { AUTH_PROCESS } = i18n();

    if (!username || !password) {
        throw new Error(AUTH_PROCESS.ERROR_INPUT_VALIDATION);
    }

    const passwordEncoded = pmcw.encode_utf8(password);
    if (!passwordEncoded) {
        throw new Error(AUTH_PROCESS.PASSWORD_MISSING);
    }

    const data = await srpLogin(username.toLowerCase(), password);
    const creds = get('CREDENTIALS') || {};
    // Admin app will break if we try to unlock the account.
    if (!data.is2FA && creds.authResponse.PasswordMode === 1 && !isAdmin()) {
        return unlock(creds.password);
    }
    return {
        ...data,
        is2FA: !!data.is2FA,
        isUnlockable: data.PasswordMode === UNLOCK_SESSION
    };
}

/**
 * validate the data for u2f response
 * @param {String} ClientData
 * @param {Number} ErrorCode
 * @param {String} KeyHandle
 * @param SignatureData
 * @return {Object|null} returns the object only if valid.
 */
function validateU2FResponse({ ClientData = '', ErrorCode = 0, KeyHandle = '', SignatureData = '' } = {}) {
    if (!ErrorCode && ClientData && KeyHandle && SignatureData) {
        return { ClientData, ErrorCode, KeyHandle, SignatureData };
    }
}

/**
 * Main function to login via 2FA
 * @param  {String} username
 * @param  {String} password
 * @param  {String} TwoFactorCode
 * @param  {Object} U2FResponse
 * @return {void}
 */
export async function login2FA(
    username = '',
    password = '',
    { password2FA: TwoFactorCode = '', U2FResponse = {} } = {}
) {
    const { AUTH_PROCESS } = i18n();

    const validU2FResponse = validateU2FResponse(ucFirstObject(U2FResponse));
    if (!validU2FResponse && !TwoFactorCode.length) {
        throw new Error(AUTH_PROCESS.ERROR_2FA_TOTP_INPUT);
    }

    const data = await loginProcess(username, password, TwoFactorCode, validU2FResponse);
    return {
        ...data,
        isUnlockable: data.PasswordMode === UNLOCK_SESSION
    };
}

/**
 * Returns an async promise that will be successful
 * only if the mailbox password proves correct
 * (we test this by decrypting a small blob)
 * @param  {String} pwd                  password
 * @param  {String} options.PrivateKey
 * @param  {String} options.AccessToken
 * @param  {String} options.RefreshToken
 * @param  {String} options.Uid
 * @param  {Number} options.ExpiresIn
 * @param  {String} options.EventID
 * @param  {String} options.KeySalt
 * @return {Promise}
 */
async function unlockWithPassword(
    pass,
    { PrivateKey = '', AccessToken = '', RefreshToken = '', UID = '', ExpiresIn = 0, EventID = '', KeySalt = '' } = {}
) {
    if (!pass) {
        throw new Error('Password is required.');
    }

    const pwd = await computeKeyPassword(pass, KeySalt);
    const { token, password } = await pmcw.checkMailboxPassword(PrivateKey, pwd, AccessToken);
    userModel.savePassword(password);
    authModel.storeData({
        AccessToken: token,
        RefreshToken,
        UID,
        ExpiresIn,
        EventID
    });
    upgradePassword();
}

/**
 * Main function to unlock the mailbox of an user
 * @param  {String} mailboxPassword
 * @param  {Object} defaultsAuthResponse custom config (optional)
 * @return {Promise}
 */
export async function unlock(mailboxPassword = '', defaultsAuthResponse = {}) {
    try {
        const { authResponse = defaultsAuthResponse } = get('CREDENTIALS') || {};
        await unlockWithPassword(mailboxPassword, authResponse);
        await setAuthCookie(authResponse);
        const loggedIn = !!authModel.read('UID');
        if (loggedIn === true && !authModel.isHeadersSet()) {
            authModel.setAuthHeaders();
        }
        return {};
    } catch (e) {
        throw e;
    }
}

/**
 * Removes all connection data
 */
export async function logout() {
    const data = await authApi.revoke();
    clearTempStorage();
    clear();
    authModel.clear();
    dispatcher.change('logout');
    return data;
}



