import _ from 'lodash';
import { addLocale, useLocale, c } from 'c-3po';

import request, { setHeaders } from './utils/request';

const CACHE = {
    i18n: {
        addLocale: _.noop,
        useLocale: _.noop
    }
};

const setAppI18n = (addLocale = _.noop, useLocale = _.noop) => {
    CACHE.i18n.addLocale = addLocale;
    CACHE.i18n.useLocale = useLocale;
};

const setConfig = (data = {}) => {
    CACHE.config = data;
    setHeaders({
        'x-pm-appversion': `${data.clientID}_${data.app_version}`,
        'x-pm-apiversion': data.api_version,
        Accept: 'application/vnd.protonmail.v1+json'
    });
};
const getConfig = (key) => _.get(CACHE.config, key);

const loadI18n = async (key) => {
    const { i18n } = CACHE;

    // Default translation we don't need to load anything
    if (key !== 'en_US') {
        const { data } = await request.get(`i18n/${key}.json`);
        addLocale(key, data);
        i18n.addLocale(key, data);
    }
    useLocale(key);
    i18n.useLocale(key);
};

export default {
    setConfig,
    getConfig,
    setAppI18n,
    loadI18n
};



