function getErrorObject() {
    try {
        throw new Error('');
    } catch (err) {
        return err;
    }
}

const getLine = () => {
    const err = getErrorObject();
    const stack = err.stack
        .split('\n')
        .slice(4)
        .map((line) => {
            return line.replace(/ at /, '').trim();
        })
        .join('\n');

    return `Stacktrace  \n${stack}`;
};

export default function log(type, data, detail) {
    const group = `%c[${type}]%c  %c${detail || 'detail'}`;

    console.groupCollapsed(group, 'background:#444;color:white', 'color:#111', 'color:green');

    if (data instanceof Error) {
        console.error(data);
    } else {
        console.log(data);
        console.log(getLine());
    }
    console.groupEnd(group, 'background:#444;color:white', 'color:#111', 'color:green');
}



